@charset "UTF-8";
/* =====  CSS reset  ===== */
html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, font, img, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td {
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  font-size: 100%;
  vertical-align: baseline;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

ul {
  list-style: none;
}

h1, h2, h3, h4, h5 {
  font-weight: normal;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after, q:before, q:after {
  content: '';
  content: none;
}

/* remember to define focus styles! */
:focus {
  outline: 0;
}

/* remember to highlight inserts somehow! padding: 9px 17px 9px 17px; */
ins {
  text-decoration: none;
}

del {
  text-decoration: line-through;
}

/* tables still need 'cellspacing="0"' in the markup */
table {
  border-collapse: collapse;
}

* {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}

*, * focus {
  outline: none;
  margin: 0;
  padding: 0;
}

select {
  line-height: 27px !important;
}

input, select, textarea {
  outline: 0;
  padding: 5px 8px;
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  -webkit-user-modify: inherit;
}

input[type="checkbox"], input[type="radio"], label {
  height: auto;
  vertical-align: middle;
  border: 0;
}

input[readonly], input[disabled], input.readonly, input.disabled, select[readonly], select[disabled], select.readonly, select.disabled, textarea[readonly], textarea[disabled], textarea.readonly, textarea.disabled {
  color: #888 !important;
  background-color: #eee;
}

img {
  border: 0;
  max-width: 100%;
  vertical-align: middle;
}

ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

p {
  margin: 0;
  padding: 0;
  letter-spacing: 0px;
}

:focus {
  outline: 0;
}

a {
  text-decoration: none !important;
  outline: none !important;
  -webkit-transition: none;
  -moz-transition: none;
  transition: none;
  color: #000000;
}

a:hover, a:active, a:focus {
  color: inherit;
  cursor: pointer;
  display: inline-block;
}

label {
  font-weight: normal;
}

.normal {
  font-style: normal;
}

.normalFont {
  font-style: normal;
  font-weight: normal;
}

article, aside, canvas, details, figcaption, figure, footer, header, hgroup, menu, nav, section, summary {
  display: block;
}

/* ==========  Correction classes ========== */
input::-moz-focus-inner {
  border: 0;
  padding: 0;
}

button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

a.button::-moz-focus-inner {
  border: 0;
  padding: 0;
}

.container:before, .container:after {
  content: " ";
  display: table;
}

*:before, *:after {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}

.clearfix:after {
  visibility: hidden;
  display: block;
  font-size: 0;
  content: " ";
  clear: both;
  height: 0;
}

.clearfix {
  display: inline-block;
}

/* start commented backslash hack \*/
* html .clearfix {
  height: 1%;
}

.clearfix {
  display: block;
}

/* close commented backslash hack */
