.radio-control {
  margin-bottom: 1rem;
}

.radio-control .helper {
  position: absolute;
  top: -0.25rem;
  left: -0.25rem;
  cursor: pointer;
  display: block;
  font-size: 1rem;
  user-select: none;
  color: #2C3848;
}

.radio-control .helper::before, .radio-control .helper::after {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  margin: 0.25rem;
  width: 1rem;
  height: 1rem;
  border-radius: 50%;
  border: 0.125rem solid currentColor;
}

.checkbox-control {
  margin-top: 0px;
  margin-bottom: 0px;
  font-size: inherit;
}

.checkbox-control .helper {
  color: #2C3848;
  position: absolute;
  top: 0;
  left: 0;
  width: 1rem;
  height: 1rem;
  z-index: 0;
  border: 1px solid currentColor;
  border-radius: 0.0625rem;
  transition: border-color 0.28s ease;
}

.checkbox-control .helper::before, .checkbox-control .helper::after {
  position: absolute;
  height: 0;
  width: 0.1rem;
  background-color: #2C3848;
  display: block;
  transform-origin: left top;
  border-radius: 0.25rem;
  content: "";
  transition: opacity 0.28s ease, height 0s linear 0.28s;
  opacity: 0;
}

.checkbox-control .helper::before {
  top: 0.75rem;
  left: 0.38rem;
  transform: rotate(-135deg);
}

.checkbox-control .helper::after {
  top: 0.5rem;
  left: 2px;
  transform: rotate(-45deg);
}

.checkbox-control label:hover .helper {
  color: #2C3848;
}

.checkbox-control input:checked ~ .helper {
  color: #2C3848;
}

.checkbox-control input:checked ~ .helper::after, .checkbox-control input:checked ~ .helper::before {
  opacity: 1;
  transition: height 0.28s ease;
}

.checkbox-control input:checked ~ .helper::after {
  height: 0.3rem;
}

.checkbox-control input:checked ~ .helper::before {
  height: 0.7rem;
  transition-delay: 0.28s;
}

.radio-control + .radio-control,
.checkbox-control + .checkbox-control {
  margin-top: 1rem;
}

